<template>
    <home-container :with-header='true' default-href="/home/wallet">
        <div class="d-flex justify-content-center flex-column align-items-center ion-margin-vertical">
            <ion-text><h1 class="font-weight-bolder h1">Scan to Receive</h1></ion-text>
            <ion-img class='bg-transparent' src='/assets/icon/qrcode.png' style='width:60%'></ion-img>
        </div>
        <div class='text-center'>
            <ion-label>Address</ion-label>
            <ion-item @click.prevent="openToast" class='border ion-margin-bottom' lines='none' button :detail="false"> 
                <ion-label class='ion-text-wrap text-center ion-text-uppercase'>92298dc9-fc52-4522-8a4a-e4c92a99b6ab</ion-label>
                <ion-button slot='end' size='small' fill='clear'>
                    <ion-icon  slot="icon-only" :icon="copyOutline"></ion-icon>
                </ion-button>
            </ion-item>
        </div>
        <ion-button strong class='ion-padding-horizontal' expand="block">
            <ion-icon  slot="start" :icon="shareSocialOutline"></ion-icon>
            <ion-label>Share Address</ion-label>
        </ion-button>
    </home-container>
</template>
<script>
import { defineComponent } from "vue";
import { shareSocialOutline,copyOutline} from "ionicons/icons";
import { useRouter } from "vue-router";
import { toastController } from '@ionic/vue';

    export default defineComponent({
        components: { },
        data() {
            return {
            }
        },
        setup() {
            const router = useRouter();
            return {
                router,shareSocialOutline,copyOutline
            };
        },
        methods: {
            async openToast() {
                const toast = await toastController
                    .create({
                    message: 'Copied!',
                    duration: 2000,
                    color:'dark'
                    })
                return toast.present();
            }
        }
});

</script>
